@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=M+PLUS+1p:wght@300&family=Noto+Sans+JP&display=swap);
* {
    margin: 0;
    padding: 0;
}

html {
  font-size: 10px;
}
h1{
  font-size: 5rem;
}
p {
  font-size: 2.5rem;
}

@media screen and (max-width: 640px) {
    html {
      font-size: 7px;
    }
}
:root {
    --main-light-bg-color:#f4f5f7;
    --main-dark-bg-color: #282c34;
    --main-light-font-color: white;
    --main-light-theme-color: #00e091;
    --main-dark-font-color: #282c34;
    --main-dark-theme-color: #1aac5d;
}

html {
  margin: 0;
  padding: 0;
}

.app {
    text-align: center;
    background-color: #f4f5f7;
    background-color: var(--main-light-bg-color);
    height: 100%;
}

body {
    font-family: "Noto Sans JP", sans-serif;
}

.dark-theme-color {
  color: #1aac5d;
  color: var(--main-dark-theme-color);
}

.light-theme-color {
  color: #00e091;
  color: var(--main-light-theme-color);
}

.dark-bg-color {
  background-color: #282c34;
  background-color: var(--main-dark-bg-color);
}

.p-large {
  font-size: 3.5rem;
}

a:hover {
  text-decoration: none;
}
a {
  position: relative;
  display: inline-block;
  transition: .3s;
  color: #00e091;
  color: var(--main-light-theme-color);
}
a::before,
a::after {
  position: absolute;
  content: '';
  width: 0;
  height: 1px;
  background-color: #282c34;
  background-color: var(--main-dark-bg-color);
  transition: .3s;
}
a::before {
  top: 0;
  left: 0;
}
a::after {
  bottom: 0;
  right: 0;
}
a:hover::before,
a:hover::after {
  width: 100%;
}
.header {
    position: relative;
    padding-top: 25rem;
}

.header .headline-body {
    border: 0px; /* ヘッダのheadlineは下線を除去 */
}

.header .section-body{
    margin: 0 auto;
    padding-top: 5rem;
}

.header .section-image {
    z-index: 3;
    position: absolute;
    top: 5rem;
    left: 50%;
    transform: translate(-50%, 0);
    -webkit-transform: translate(-50%, 0);
    -ms-transform: translate(-50%, 0);
}

.header-bg {
    background-color: var(--main-dark-bg-color);
    width: 100vw;
    height: 20rem;
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
}
.headline {
    --headline-spacing: 0.8rem;
    font-family: "Lato", sans-serif;
}

.headline-body {
    margin: 0;
    display: inline-block;
    font-size: 6rem;
    text-align: center;
    letter-spacing: var(--headline-spacing);
    vertical-align: middle;
    padding: 0 1.5rem;
    border-bottom: 0.2rem solid var(--main-dark-font-color);
}

.headline-fontawesome {
    margin-right: calc(var(--headline-spacing) + 1rem);
    font-size: 4rem;
}

.headline-image {
    width: 0.8em;
    margin: 0;
    margin-right: var(--headline-spacing);
    border-radius: 30%;
    border: var(--main-dark-bg-color) 1px solid;
    vertical-align: middle;
}
.section-body {
    width: 80vw;
    margin: auto;
    margin-top: 5rem;
    text-align: left;
    padding: 0 2rem;
}

.section-image {
    height: 25rem;
    display: block;
    margin: auto;
    border-radius: 100%;
    box-shadow: 0.1rem 0.1rem 2rem;
}

.section-not-first {
    margin-top: 10rem;
}

@media screen and (max-width: 640px) {
    .section-body {
        width: 95vw;
        margin-top: 3rem;
    }
}
.experiences {
    letter-spacing: 0.5rem;
}

@media screen and (max-width: 640px) {
    .experiences {
        letter-spacing: normal;
    }
}
.list {
    margin: 0;
    padding: 0;
}

.list-fontawesome {
    margin-right: 1rem;
}
.sns-link-text {
    margin: 0;
    padding: 0;
}
.fields .section-body p {
    letter-spacing: 0.4rem;
}

@media screen and (max-width: 640px) {
    .fields .section-body p {
        letter-spacing: normal;
    }
}
.lang-section-body {
    width: 90%;
    margin: auto;
    letter-spacing: 1.5rem;
}

@media screen and (max-width: 640px) {
    .lang-section-body {
        letter-spacing: 1rem;
    }
}
.product {
    width: 100%;
    margin-bottom: 7rem;
}

.product-description {
    font-size: 2rem;
}

.product-name {
    border-bottom: solid 0.1rem var(--main-dark-font-color);
}

.product-techs {
    padding: 0;
}

.product-tech {
    font-size: 1.5rem;
    margin: 0.5rem;
}

.product-link {
    font-size: 1.5rem;
    margin: 0;
}

.product-details {
    margin-bottom: 1rem;
}

.product-view {
    width: 100%;
    bottom: 0;
}

.product-image {
    margin: 0;
    width: 100%;
}

.product-video {
    margin: 0;
    padding: 0;
    position: relative;
    width: 100%;
}

.product-video:before {
    content: "";
    display: block;
    padding-top: 56.25% /*16:9固定*/
}

.youtube-frame {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 640px) {
    .product-tech {
        margin: 0.3rem;
    }
}
.footer {
    background-color: var(--main-dark-bg-color);
    margin: 0;
    padding: 1.5rem;
    color: var(--main-light-font-color);
}
